<template>
  <el-menu class="el-menu-demo" mode="horizontal" :router="true">

    <el-menu-item index="/" v-if="!admin"></el-menu-item>

    <el-menu-item index="/verify" v-if="admin&&logged"></el-menu-item>

    <el-menu-item index="/cart" v-if="(!admin)&&logged"></el-menu-item>

    <el-menu-item index="/purchased" v-if="(!admin)&&logged"></el-menu-item>

    <el-menu-item index="/addProduct" v-if="logged"></el-menu-item>

    <el-menu-item index="/Uploaded" v-if="logged"></el-menu-item>

    <el-menu-item index="/login" v-if="!logged"></el-menu-item>

    <el-submenu index="" v-if="logged">
      <template slot="title"></template>
      <el-menu-item index="/login" @click.native="logout()"></el-menu-item>
      <el-menu-item index="/profile"></el-menu-item>
    </el-submenu>

  </el-menu>
</template>
<script>
  export default {
    data() {
      return {
        logged: false,
        username: '',
        credit: '',
        admin: false
      }
    },
    methods: {
      userState() {
        var _this = this;
        this.logged = localStorage.getItem('logged');
        if (this.logged == true) {
          _this.username = localStorage.getItem('nickName');
        }
        let ad = localStorage.getItem('userId');
        if (ad==1){
          _this.admin = true;
        }
        else{
          _this.admin = false;
        }
      },
      logout() {
        localStorage.clear();
      }
    },
    created() {
      this.userState();
    }
  }
</script>



