<template>
  <div>
    <elnav></elnav>
    <el-row>
      <el-col :span="20" :offset="2">
        <h2>{{good.Title}}</h2>
        <el-row>
          <el-col :span="14">
            <iframe v-bind:src="PPTurl" width="100%" height="500" frameborder="0" scrolling="no">
              <a src=""></a>
            </iframe>
          </el-col>
          <el-col :span="9" :offset="1">
            <el-card class="box-card">
              <el-row class="col_margin">
                <el-col :span="12">
                  <h3></h3>
                  <el-tag>{{t[good.Type-1]}}</el-tag>
                </el-col>
                <el-col :span="12">
                  <h3></h3>
                  <el-tag>{{w[good.Way-1]}}</el-tag>
                </el-col>
                <el-col>
                  <h3>PPT</h3>
                  {{good.Description}}
                </el-col>
                <el-col>
                  <h3></h3>
                  {{good.value}}
                </el-col>
              </el-row>
              <el-row v-show="!visiable">
                <el-col>
                  <el-button type="success" round v-on:click="purchase()"></el-button>
                  <el-button type="danger" round v-on:click="addCart()"><i class="fa fa-shopping-cart"
                                                                           aria-hidden="true"></i>&nbsp;
                  </el-button>
                </el-col>
              </el-row>
              <el-row v-show="visiable">
                <el-col>
                  <el-button type="primary" v-on:click="sendMail()"><i class="fa fa-share" aria-hidden="true"></i>&nbsp;
                  </el-button>
                  <el-button v-on:click="download()"><i class="fa fa-cloud-download" aria-hidden="true"></i>&nbsp;
                  </el-button>
                </el-col>
              </el-row>
            </el-card>

          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<style>
  .col_margin {
    margin-bottom: 2rem;
  }
</style>
<script>
  import elnav from './el-nav.vue'
  import Axios from 'axios'

  export default {
    components: {
      elnav
    },
    data() {
      return {
        visiable: true,
        w: ["", "", "", "", "", "", ""],
        t: ["", "", "", "", "iOS", "", ""],
        good: {
          id: '',
          Title: '',
          Description: '',
          Thumbnail: '',
          Download: '',
          FavoriteCount: '',
          Type: '',
          Way: '',
          value: ''
        },
        PPTurl: ''
      }
    },
    methods: {
      setVisible() {
        let _this = this;
        let params = new URLSearchParams();
        params.append('Uid', localStorage.getItem('userId'));
        params.append('Pid', _this.$route.params.id);
        let url = '/api/visiable';
        this.$http.post(url, params).then(function (res) {
          if (res.data.status == 1) {
            _this.visiable = true;
          }
          else {
            _this.visiable = false;
          }
        })
      },
      getData() {
        var _this = this;
        var url = 'api/getProudctDetail?id=' + this.$route.params.id;
        this.$http.get(url).then(function (res) {
          var response = res.data;
          if (response.status == 1) {
            _this.good = response.data[0];
            _this.PPTurl = "http://ow365.cn/?i=13431&furl=" + response.data[0].Download;
          }
          else {
            _this.$message.error('');
            _this.$router.push({path: '/'});
          }
        })
      },
      sendMail() {
        var _this = this;
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: ''
        }).then(({value}) => {
          const loading = _this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          var url = '/api/send?id=' + this.good.id + '&email=' + value;
          _this.$http.get(url).then(function (res) {
            if (res.data.status == 1) {
              loading.close();
              _this.$message({
                type: 'success',
                message: ''
              });
            }
            else {
              loading.close();
              this.$message.error('');
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      download() {
        var _this = this;
        this.$alert(_this.good.Download, '', {
          confirmButtonText: ''
        });
      },
      purchase() {
        var _this = this;
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({value}) => {
          const loading = _this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          var url = '/api/Signalpurchase';
          var params = new URLSearchParams();
          params.append('Uid', localStorage.getItem('userId'));
          params.append('Pid', _this.good.id);
          params.append('Password', value);
          _this.$http.post(url, params).then(function (res) {
            if (res.data.status == 1) {
              loading.close();
              _this.visiable = true;
              _this.$message({
                type: 'success',
                message: ''
              });
            }
            else {
              _this.visiable = false;
              loading.close();
              _this.$message.error(res.data.msg);
            }
          })
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      addCart() {
        var _this = this;
        var url = 'api/addToCart';
        var params = new URLSearchParams();
        params.append('productId', this.good.id);
        params.append('userId', localStorage.getItem('userId'));
        _this.$http.post(url, params)
          .then(function (res) {
            if (res.data.status == 1) {
              _this.$message({
                type: 'success',
                message: ''
              });
            }
            else {
              _this.$message.error(res.data.msg);
            }
          })
      }
    },
    created() {
      this.getData();
      this.setVisible();
    }
  }
</script>



